import { error } from "@sveltejs/kit";
const load = async ({ fetch, params }) => {
  const res = await fetch(`/api/dinosaurs/${params.dinosaur}`);
  const dinosaur = await res.json();
  if (res.status === 404) {
    return error(404, "No dinosaur found");
  }
  return { dinosaur };
};
export {
  load
};
